<template>
	<view class="tabs_item tabs_box" v-if="tagData">
		<barDropDown :chartData="chartData" :unitText="'人次'" :timeUnit="timeUnit" :timeSelect="timeSelect"
			 @prevTap="_prevTap" @nextTap="_nextTap" @unitTap="_unitTap"></barDropDown>
		
		<view class="case_wrap">
			<view class="case_box case_follow">
				<view class="title">跟进情况</view>
				<view class="count_box">
					<view class="count_item" v-for="(item,index) in tagData.followUpData" :key="index">
						<text>{{item.count}}</text>
						<text>{{item.name}}</text>
					</view>
				</view>
			</view>
			<view class="case_box">
				<view class="title">销售情况</view>
				<view class="count_box">
					<view class="count_item" v-for="(item,index) in tagData.salesData" :key="index">
						<text>{{item.count}}</text>
						<text>{{item.name}}</text>
					</view>
				</view>
			</view>
		</view>
		<view class="case_wrap caseNo">
			<view class="case_box">
				<view class="title">逾期情况</view>
				<view class="count_box">
					<view class="count_item" v-for="(item,index) in tagData.overdueData" :key="index">
						<text>{{item.count}}</text>
						<text>{{item.name}}</text>
					</view>
				</view>
			</view>
		</view>
	</view>
</template>

<script>
	import barDropDown from '@/components/barDropDown.vue';
	export default {
		props:['chartData', 'timeUnit', 'timeSelect', 'tagData'],
		data() {
			return {}
		},
		mounted() {
			
		},
		methods:{
			// 柱状图左翻
			_prevTap() {
				this.$emit('prevTap')
			},
			// 柱状图右翻
			_nextTap() {
				this.$emit('nextTap')
			},
			// 柱状图单位选择
			_unitTap(index) {
				this.$emit('unitTap',index)
			}
		},
		components:{
			barDropDown
		}
	}
</script>

<style lang="scss">
	.tabs_box {
		margin-top: 0;
		.case_wrap{
			display: flex;
			justify-content: space-between;
			align-items: center;
			flex: 1;
			padding: 30rpx;
			.case_box{
				width: 100%;
				background: #FFFFFF;
				border-radius: 16rpx;
				.title{
					font-size: 30rpx;
					padding: 20rpx 30rpx;
					border-bottom: 1rpx solid #f5f5f5;
					font-weight: bold;
				}
				.count_box {
					border-radius: 10rpx;
					padding: 30rpx;
					display: flex;
					justify-content: space-between;
					
					.count_item {
						display: flex;
						flex-direction: column;
						flex: 1;
						align-items: center;
					
						text {
							font-size: 48rpx;
							color: #03958C;
							font-weight: bold;
						}
					
						text:last-child {
							font-size: 24rpx;
							margin-top: 10rpx;
							color: #333;
							font-weight: normal;
						}
					}
					
					.count_item:last-child {
						border: none;
					}
				}
			}
			.case_follow{
				margin-right: 24rpx;
				width: 230rpx;
				min-width: 230rpx;
			}
		}
		.caseNo{
			padding: 0 30rpx 30rpx;
		}
	}
</style>
